import React from 'react';

import './separatorLine.css';

function SeparatorLine() {
	return (
		<div className='separatorLine'>
			<div className='separatorLine__dot'></div>
			<div className='separatorLine__line'></div>
			<div className='separatorLine__dot'></div>
		</div>
	);
}

export default SeparatorLine;
